export default {
    props: {
        start: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            timer: 60,
            bar: ''
        }
    },
    watch: {
        start() {
            if (this.start) {
                this.bar = setInterval(() => {
                    if (this.timer > 1) {
                        this.timer--;
                    } else {
                        this.refresh();
                    }
                }, 1000);
            } else {
                this.refresh();
            }
        }
    },
    methods: {
        clickBuntton() {
            if (!this.start) {
                this.$emit('click', false);
            }  
        },
        refresh() {
            this.timer = 60;
            clearInterval(this.bar);
            this.$emit('refresh');
        }
    },
    beforeMount() {
        this.refresh();
    },
}
